import React from 'react';
import './SearchForm.css';

function SearchForm({ handleSerchNews }) {

  const [keyword, setKeyword] = React.useState('');
  const [searchErrorMessage, setSearchErrorMessage] = React.useState('');

  function handleChangeKeyword(e) {
    setKeyword(e.target.value);
  }

    function handleSubmit(e) {
        e.preventDefault();
        if (!keyword) {
          setSearchErrorMessage('–ù—É–∂–Ω–æ –≤–≤–µ—Å—Ç–∏ –∫–ª—é—á–µ–≤–æ–µ —Å–ª–æ–≤–æ');
          return;
        }
        handleSerchNews(keyword)
        setSearchErrorMessage('');
      }

    return (

        <form className='search-form' name='search-form' noValidate onSubmit={handleSubmit}>
            <h1 className='search-form__title'>–ß—Ç–æ —Ç–≤–æ—Ä–∏—Ç—Å—è –≤ –º–∏—Ä–µ?</h1>
            <p className='search-form__description'>
                –ù–∞—Ö–æ–¥–∏—Ç–µ —Å–∞–º—ã–µ —Å–≤–µ–∂–∏–µ —Å—Ç–∞—Ç—å–∏ –Ω–∞ –ª—é–±—É—é —Ç–µ–º—É –∏ —Å–æ—Ö—Ä–∞–Ω—è–π—Ç–µ –≤ —Å–≤–æ—ë–º –ª–∏—á–Ω–æ–º –∫–∞–±–∏–Ω–µ—Ç–µ.
                </p>
            <div className='search-form__input-container'>
                <input className='search-form__input' placeholder='–í–≤–µ–¥–∏—Ç–µ —Ç–µ–º—É –Ω–æ–≤–æ—Å—Ç–∏' name='search-text' value={keyword || ''} required  onChange={handleChangeKeyword}/>
                <span id='search-error' className='search__input-error'>{searchErrorMessage}</span>
                <button type='submit' className='search-form__button-find'>–ò—Å–∫–∞—Ç—å</button>
            </div>
        </form>

    )
}

export default SearchForm;